@import "../../../scss/global";
@import "../../../scss/mixins/mixin-input";

.InputControl {
    width: 100%;
    position: relative;

    &__input {
        @include mixin-input;
        width: 100%;
        text-align: center;
    }

    &__icon {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 8px;
        width: 8px;
        font-size: $DEFAULT_FONT_SIZE_XS;
        right: $DEFAULT_MARGIN_M;
        color: var(--color-grey-300);
        display: none;

        &#{&}--visible {
            display: block;
        }
    }
}